import React from 'react';
import { Link } from 'gatsby';


import {FaFacebookSquare , FaInstagram , FaLinkedin , FaYoutube , FaMobileAlt  , FaEnvelope } from 'react-icons/fa';
import './HeaderTop.css';

const HeaderTop = ({data}) => { 
    console.log(data)
    return(
        <div className='header-top' >
       
                <div className='header-item'>

                <Link to={data.title} ><FaFacebookSquare /></Link> 
                <Link to={data.section2} > <FaInstagram/></Link> 
                <Link to={data.subtitle} ><FaLinkedin/></Link> 
                <Link to={data.section1} ><FaYoutube/></Link> 
               
                </div>
                <div className='header-item'>
                <FaMobileAlt/> {data.phone}
                </div>
                <div className='header-item'>
                <FaEnvelope/> Email                
                </div>
        </div>
    );
}

export default HeaderTop;


